var audioArr = {
	audio_1: 'data:audio/mpeg;base64,',
	audio_2: 'data:audio/mpeg;base64,',
	audio_3: 'data:audio/mpeg;base64,',
	audio_4: 'data:audio/mpeg;base64,'
}

function respond(src) {
	// 短震动
	uni.vibrateShort({});
	// 播放提示音
	let innerAudioContext = uni.createInnerAudioContext();
	innerAudioContext.autoplay = true;
	innerAudioContext.src = src;
	innerAudioContext.onError((res) => {
		return false;
	});
	innerAudioContext.play();
	return true;
}

function palys(type = 'tips') {
	switch (type) {
		case 'tips':
			respond(audioArr.audio_1);
			break;
		default:
			break;
	}
}

export {
	palys
}